<template>
    <div class="default-page-container" @click="bodyClick">
        <div class="header">

            <div class="header-container">
                <div class="logo" @click="goPage('/')">
                    <img src="../../../assets/img/logo.png">
                </div>
                <div class="menu-pannel pc-element">
                    <ul class="menu">
                        <li class="menu-item" v-for="(menu,index) in menuList" :class="menu.active" :key="menu.code">
                            <div class="menu-name">{{ menu.mname }}</div>
                            <ul class="sub-menu" v-if="menu.subList.length>0">
                                <li class="sub-menu-item" v-for="(subMenu,subIndex) in menu.subList"
                                    :class="subMenu.active" :key="subMenu.code"
                                    @click="goSubMenuPage(menu.code,subMenu.code,subMenu)">
                                    {{ subMenu.mname }}
                                </li>
                            </ul>
                        </li>
                    </ul>
                    <div class="sub-menu-screen">
                    </div>
                </div>
                <div class="pc-element">
                    <ul class="add-menu" v-if="!isLogin">
                        <li @click="goPage('/joinIntro')">
                            회원가입
                        </li>
                        <li class="dot">
                            <div></div>
                        </li>
                        <li @click="goPage('/memberLogin')">
                            로그인
                        </li>

                    </ul>
                    <ul class="add-menu" style="width:260px" v-else>
                        <li @click="showUserNotice()">
                            <div class="my-notice-title">
                                <div class="ntitle">알림</div>
                                <div class="new" v-if="memberNoticeList.length>0">N</div>
                            </div>
                            <div class="mynotice-list">
                                <div class="item-title-box">
                                    <div class="item-title">알림</div>
                                    <div class="item-close">
                                        <font-awesome-icon icon="fa-solid fa-xmark"/>
                                    </div>
                                </div>
                                <div class="item-list" v-if="memberNoticeList.length>0">
                                    <div class="item" v-for="item in memberNoticeList" @click="goViewArticle(item)">
                                        <span class="cmtspan" v-if="item.gtype=='comment'">[댓글]</span>
                                        <span class="namespan" v-if="item.gtype=='comment'">{{item.userName}}</span>
                                        {{item.noticeContent}}
                                    </div>

                                </div>
                                <div class="no-data" v-else>
                                    새로운 알림이 없습니다
                                </div>
                            </div>
                        </li>
                        <li class="dot">
                            <div></div>
                        </li>
                        <li @click="showMyPannel()">
                            <div class="my-notice-title">
                                <div class="ntitle">책갈피</div>
                                <div class="myicon">
                                    <img v-if="userImg" :src="userImg">
                                    <font-awesome-icon v-else class="icon" icon="fa-solid fa-user"/>
                                </div>
                            </div>
                            <div class="mypage-list">
                                <div class="item" @click="goPage('/mypage/memberModify')">계정정보</div>
                                <div class="item" @click="goPage('/mypage/main')">활동정보 보기</div>
                                <div class="item" @click="goPage('/mypage/memberPassword')">비밀번호 변경</div>
                                <div v-if="role == 'ROLE_ADMIN'" class="item" @click="goPage('/admin')">관리자</div>
                                <div class="item" style="color:#333;" @click="logOut()">로그아웃</div>
                            </div>
                        </li>

                    </ul>
                </div>
                <div class="tablet-mobile-element">
                    <div class="add-menu" @click="showMenu()">
                        <font-awesome-icon icon="fa-solid fa-bars" class="icon"/>
                    </div>

                </div>
            </div>
        </div>


        <!-- mobile tablet menu -->
        <div class="tablet-mobile-element menu-pannel-layout" v-if="menuPannelShow">
            <div class="virtual-click" @click="menuHide()"></div>
            <div class="tbm-menu-box">
                <ul class="add-menu" v-if="!isLogin">
                    <li class="user-name"></li>
                    <li @click="goPage('/joinIntro')">
                        회원가입
                    </li>
                    <li class="dot">
                        <div></div>
                    </li>
                    <li @click="goPage('/memberLogin')">
                        로그인
                    </li>

                </ul>
                <ul class="add-menu" v-else>
                    <li class="user-name">
                        <div class="myicon">
                            <img v-if="userImg" :src="userImg">
                            <font-awesome-icon v-else class="icon" icon="fa-solid fa-user"/>
                        </div>
                        <div class="user-id">
                            {{userId}}
                        </div>
                    </li>
                    <li @click="goPage('/mypage/main')">
                        <div class="my-notice-title">
                            <div class="ntitle">알림</div>
                            <div class="new" v-if="memberNoticeList.length>0">N</div>
                        </div>
                    </li>
                    <li class="dot">
                        <div></div>
                    </li>
                    <li @click="goPage('/mypage/memberModify')">
                        책갈피
                    </li>

                </ul>
                <div class="search-box">
                    <form novalidate @submit.prevent="menuSearchValidate">
                        <div class="search-box-outline">
                            <ul class="keyword-list" id="menu-keyword-list">
                                <li @click="setMenuKeyword(item.name)" v-for="item in keywordList">{{item.name}}</li>
                                <li style="cursor: default" class="no-data" v-if="keywordList.length<1">
                                    인기 검색어가<br>존재하지 않습니다
                                </li>
                            </ul>
                            <div class="select-box tablet-element" @click="showMenuKeyword()">
                                <div class="text">인기 검색어</div>
                                <div class="icon">
                                    <font-awesome-icon icon="fa-solid fa-sort-down" v-if="!keywordShow"/>
                                    <font-awesome-icon class="up" icon="fa-solid fa-sort-up" v-else/>
                                </div>
                            </div>

                            <div class="search-input">
                                <div class="input">
                                    <input type="text" v-model="keyword" class="search-input-text">
                                </div>
                                <div class="img-button">
                                    <button type="submit">
                                        <font-awesome-icon icon="fa-solid fa-magnifying-glass"/>
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div class="search-error" v-if="errorMessage">
                            {{errorMessage}}
                        </div>
                    </form>
                    <div class="keyword-list-round-box mobile-element">
                        <ul class="keyword-list-round">
                            <li @click="setKeyword(item.name)" v-for="item in keywordList">
                                <div>#{{item.name}}</div>
                            </li>
                        </ul>
                    </div>
                </div>

                <ul class="menu">
                    <li class="menu-item" v-for="(menu,index) in menuList" :class="menu.active" :key="'tbm'+menu.code">
                        <div class="menu-item-list" @click="tbmMenuPage(menu.code)">
                            <div class="menu-name">
                                {{ menu.mname }}
                            </div>
                            <div class="icon">
                                <font-awesome-icon icon="fa-sharp fa-solid fa-angle-up" v-if="menu.active=='active'"/>
                                <font-awesome-icon icon="fa-sharp fa-solid fa-angle-down" v-else/>
                            </div>
                        </div>
                        <ul class="sub-menu" :id="'tbm-sub-'+menu.code" v-if="menu.subList.length>0">
                            <li class="sub-menu-item" v-for="(subMenu,subIndex) in menu.subList"
                                :class="subMenu.active" :key="subMenu.code"
                                @click="goSubMenuPage(menu.code,subMenu.code,subMenu)">
                                {{ subMenu.mname }}
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        </div>
        <!--  mobile tablet menu end -->


        <div :class="'content-body '+contentBodyClass">
            <div class="search-box" v-if="searchShow">
                <form novalidate @submit.prevent="validate">
                    <div class="search-box-outline">
                        <ul class="keyword-list" id="keyword-list">
                            <li @click="setKeyword(item.name)" v-for="item in keywordList">{{item.name}}</li>
                            <li style="cursor: default" class="no-data" v-if="keywordList.length<1">
                                인기 검색어가<br>존재하지 않습니다
                            </li>
                        </ul>
                        <div class="select-box pc-tablet-element" @click="showKeyword()">
                            <div class="text">인기 검색어</div>
                            <div class="icon">
                                <font-awesome-icon icon="fa-solid fa-sort-down" v-if="!selectShow"/>
                                <font-awesome-icon class="up" icon="fa-solid fa-sort-up" v-else/>
                            </div>
                        </div>

                        <div class="search-input">
                            <div class="input">
                                <input type="text" v-model="keyword" class="search-input-text">
                            </div>
                            <div class="img-button">
                                <button type="submit">
                                    <font-awesome-icon icon="fa-solid fa-magnifying-glass"/>
                                </button>
                            </div>
                        </div>
                    </div>
                    <div class="search-error" v-if="errorMessage">
                        {{errorMessage}}
                    </div>
                </form>
                <div class="keyword-list-round-box mobile-element">
                    <ul class="keyword-list-round">
                        <li @click="setKeyword(item.name)" v-for="item in keywordList">
                            <div>#{{item.name}}</div>
                        </li>
                    </ul>
                </div>

            </div>
            <div :class="'content-container '+bottomShow">
                <div class="sub-menu pc-element" v-if="subMenuList.length>0">
                    <div class="sub-menu-title">{{menuTitle}}</div>
                    <ul class="sub-ul">
                        <li v-for="(item,index) in subMenuList" :key="'left-menu-'+item.code" :class="item.active"
                            @click="goSubPage(item.code,item)">
                            {{item.mname}}
                        </li>
                    </ul>
                </div>
                <div class="sub-menu tablet-mobile-element" v-if="myMenuShow">
                    <div class="sub-menu-title">{{menuTitle}}</div>
                    <ul class="sub-ul">
                        <li v-for="(item,index) in subMenuList" :key="'left-menu-'+item.code" :class="item.active"
                            @click="goSubPage(item.code,item)">
                            {{item.mname}}
                        </li>
                    </ul>
                </div>

                <div class="content">
                    <router-view/>
                </div>
            </div>
        </div>
        <div class="bottom" v-if="bottomShow=='show'">
            <div :class="'bottom-container '+currentUrlName">
                <div class="bottom-logo" @click="goPage('/')">
                    <img src="../../../assets/img/logo2.png">
                </div>
                <div class="bottom-copy">
                    Copyright © Invesume, Inc. All rights reserved.
                </div>
                <ul class="bottom-menu pc-element">
                    <li @click="goAgreePage('use')">이용약관</li>
                    <li class="dot"></li>
                    <li @click="goAgreePage('privacy')">개인정보처리방침</li>
                    <li class="dot"></li>
                    <li @click="goLocationMove('slack')">Slack 채널안내</li>
                    <li class="dot"></li>

                    <li @click="goLocationMove('git')">Github 참여</li>
                </ul>

            </div>
        </div>
    </div>
</template>
<script>

    import AUTH from '@/config/web/auth'
    import apiService from "@/config/web/apiService";
    import {API_URL_GET_SITE_BASE, API_URL_LOGOUT} from "@/config/web/urls";
    import {mapActions, mapGetters} from "vuex";
    import JQ from "jquery";
    import searchComponent from "@/views/WEB/search/search"


    export default {
        name: 'defaultLayout',
        computed: {
            ...mapGetters('config', ["getMemberNoticeList", "getMNoticeActive", "getMemberImg"]),
        },
        watch: {
            '$route'(to, from) {
                this.currentUrlName = to.name;
                this.setNoticeAlert();
                const mcode = (this.$route.params.mainCode) ? this.$route.params.mainCode : '';
                const scode = (this.$route.params.subCode) ? this.$route.params.subCode : '';
                if (mcode) {
                    this.mainCode = mcode;
                    this.searchShow = true;
                    switch (from.name) {
                        case 'siteMain':
                        case 'siteSearch':
                        case 'mypageMain':
                        case 'mypageMemberModify':
                        case 'mypageMemberPassword':
                            this.setSubMenu(mcode, scode)
                            break;
                    }
                    this.keyword = '';
                } else {
                    this.mainCode = '';
                    this.checkCurrentUrlName();

                }
                if (this.subMenuList.length < 1) {
                    for (let item of this.menuList) {
                        if (item.subList && item.subList.length > 0) {
                            for (let subItem of item.subList) {
                                subItem.active = '';
                            }
                        }
                        item.active = ''
                    }
                }
                this.handleResize();
            },
            searchShow(val) {
                this.contentClass = (val) ? '' : 'margin-top';
            },
            getMNoticeActive(val) {
                if (val) {
                    this.memberNoticeList = [];
                    for (const item of this.getMemberNoticeList) {
                        this.memberNoticeList.push(item);
                    }

                }
            },
            getMemberImg(val) {
                if (val) {
                    this.userImg = apiService.imgHost + 'api/controller/member/getMemberImage?imgName=' + encodeURIComponent(val)
                }
            }
        },
        data() {
            return {
                isLogin: false,
                menuList: [],
                subMenuList: [],
                currentUrlName: '',
                keyword: '',
                selectShow: false,
                keywordShow: false,
                errorMessage: '',
                searchShow: false,
                menuTitle: '',
                contentClass: 'margin-top',
                newUserNotice: true,
                userImg: '',
                memberNoticeList: [],
                boardIds: {},
                keywordList: [],
                role: '',
                userId: '',
                menuPannelShow: false,
                bottomShow: 'show',
                myMenuShow: false,
                contentBodyClass: 'default',
            };
        },
        created() {
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.isLogin = true;
                if (member.memberInfo.img) this.userImg = apiService.imgHost + 'api/controller/member/getMemberImage?imgName=' + encodeURIComponent(member.memberInfo.img)
                this.role = member.memberInfo.role;
                this.userId = member.memberInfo.uid;
            }
            this.currentUrlName = this.$route.name;
            this.checkCurrentUrlName();
            if (this.$route.params.mainCode) {
                this.searchShow = true;
            }
            this.getData()
            if (this.currentUrlName == 'siteSearch') {
                this.keyword = this.$route.params.keyword;
            } else {
                this.keyword = '';
            }
            this.handleResize();
        },
        methods: {
            ...mapActions('config', ["setAgree", "setSns", "setMenu", "setSettingActive"]),
            setNoticeAlert() {
                if (this.isLogin) {
                    const frm = {}
                    apiService.post(API_URL_GET_SITE_BASE, frm, (res) => {

                        if (res.memberNoticeList) {
                            for (let item of res.memberNoticeList.content) {
                                item.noticeContent = (item.gtype == 'comment') ? '님이 내글에 새로운 댓글을 달았습니다' : '새 공지사항이 등록되었습니다';
                                this.memberNoticeList.push(item);
                            }

                        }
                        if (res.keywordList) {
                            res.keywordList.sort(function (a, b) {
                                if (parseInt(a.hits) > parseInt(b.hits)) return -1;
                                return 1;
                            })
                            for (let item of res.keywordList) {
                                this.keywordList.push(item)
                            }

                        }

                    });
                }
            },
            checkCurrentUrlName() {
                switch (this.currentUrlName) {
                    case 'siteMain':
                    case 'siteSearch':
                        if (this.currentUrlName != 'siteSearch') {
                            this.keyword = '';
                        }
                        this.searchShow = true;
                        this.subMenuList = [];
                        break;
                    case 'mypageMain':
                    case 'mypageMemberModify':
                    case 'mypageMemberPassword':
                        this.keyword = '';
                        this.searchShow = false;
                        this.menuTitle = '계정관리'
                        const modifyActive = (this.currentUrlName == 'mypageMemberModify') ? 'active' : '';
                        const mainActive = (this.currentUrlName == 'mypageMain') ? 'active' : '';
                        const passActive = (this.currentUrlName == 'mypageMemberPassword') ? 'active' : '';

                        this.subMenuList = [
                            {code: 'mypageMemberModify', mname: '계정정보', active: modifyActive, type: 'mypage'},
                            {code: 'mypageMain', mname: '활동정보 보기', active: mainActive, type: 'mypage'},
                            {code: 'mypageMemberPassword', mname: '비밀번호 변경', active: passActive, type: 'mypage'},
                        ];

                        break;
                    default:
                        this.keyword = '';
                        this.searchShow = false;
                        this.subMenuList = [];
                        break;
                }


            },
            showMenu() {
                JQ('.menu-pannel-layout').show();
                JQ('.tbm-menu-box').animate({
                    'margin-left': '20%'
                }, 300)
            },
            showMenuKeyword() {
                if (this.keywordShow) {
                    this.keywordShow = false;
                    JQ('#menu-keyword-list').slideUp(100)

                } else {
                    this.keywordShow = true;
                    JQ('#menu-keyword-list').slideDown(100)
                }
            },
            setMenuKeyword(name) {
                this.keyword = name;
                this.keywordShow = false;
                JQ('#menu-keyword-list').slideUp(200)
            },
            menuHide() {
                JQ('.tbm-menu-box').animate({
                    'margin-left': '100%'
                }, 300, function () {
                    JQ('.menu-pannel-layout').hide();
                })


            },
            getData() {
                const frm = {}
                apiService.post(API_URL_GET_SITE_BASE, frm, (res) => {
                    if (res.setting) {
                        if (res.setting.menu) {
                            this.initMenu(res.setting.menu);
                        }
                        if (res.setting.agree) {
                            const agree = JSON.parse(res.setting.agree);
                            this.setAgree(agree)
                        }
                        if (res.setting.sns) {
                            const sns = JSON.parse(res.setting.sns);
                            this.setSns(sns)
                        }
                        this.setSettingActive(true);

                        if (this.isLogin) {
                            if (res.memberNoticeList) {
                                for (let item of res.memberNoticeList.content) {
                                    item.noticeContent = (item.gtype == 'comment') ? '님이 내글에 새로운 댓글을 달았습니다' : '새 공지사항이 등록되었습니다';
                                    this.memberNoticeList.push(item);
                                }

                            }
                        }
                        if (res.keywordList) {
                            res.keywordList.sort(function (a, b) {
                                if (parseInt(a.hits) > parseInt(b.hits)) return -1;
                                return 1;
                            })
                            for (let item of res.keywordList) {
                                this.keywordList.push(item)
                            }

                        }

                    }

                });
            },
            initMenu(orgmenu) {
                const mcode = (this.$route.params.mainCode) ? this.$route.params.mainCode : '';
                const scode = (this.$route.params.subCode) ? this.$route.params.subCode : '';
                if (mcode) this.mainCode = mcode;

                const menus = JSON.parse(orgmenu);
                const menuList = [];
                let selectedMcode = '';
                for (let menu of menus) {
                    if (menu.muse != 'yes') continue;
                    if (menu.code == mcode) {
                        selectedMcode = menu.code;
                    }
                    menu.active = '';
                    if (menu.subList && menu.subList.length > 0) {
                        let subList = [];
                        for (let subItem of menu.subList) {
                            if (subItem.muse != 'yes') continue;
                            if (!selectedMcode && subItem.code == scode) selectedMcode = menu.code;
                            subItem.active = '';
                            subList.push(subItem);
                            if (subItem.linkType == 'board') {
                                this.boardIds[subItem.boardId] = {mainCode: menu.code, subCode: subItem.code};
                            }
                        }
                        menu.subList = subList;
                    } else {
                        menu.subList = [];
                        if (menu.linkType == 'board') {
                            this.boardIds[menu.boardId] = {mainCode: menu.code};
                        }
                    }
                    menuList.push(menu)
                }
                this.setMenu(menuList);
                for (let menu of menuList) {
                    if (selectedMcode && menu.code == selectedMcode) {
                        menu.active = 'active';
                        this.menuTitle = menu.mname;
                        if (menu.subList && menu.subList.length > 0) {
                            for (let subItem of menu.subList) {
                                if (subItem.code == scode) subItem.active = 'active'
                                else subItem.active = ''


                                this.subMenuList.push(subItem);
                            }
                        }
                    } else menu.active = '';
                    this.menuList.push(menu)
                }
            },
            showKeyword() {
                if (this.selectShow) {
                    this.selectShow = false;
                    JQ('#keyword-list').slideUp(100)

                } else {
                    this.selectShow = true;
                    JQ('#keyword-list').slideDown(100)
                }
            },
            setKeyword(name) {
                this.keyword = name;
                this.selectShow = false;
                JQ('#keyword-list').slideUp(200)
            },
            goMenuPage(code) {
                this.subMenuList = [];
                for (let item of this.menuList) {
                    if (item.code == code) {

                        if (item.linkType == 'url') {
                            if (item.urlWinType == 'new') {
                                window.open(item.urlLink);
                            } else {
                                location.href = item.urlLink;
                            }
                            return;
                        }

                        this.menuTitle = item.mname;
                        if (item.subList && item.subList.length > 0) {
                            for (let subItem of item.subList) {
                                this.subMenuList.push(subItem);
                            }
                        }
                        this.mainCode = code;
                        item.active = 'active'
                        this.linkMove(item);
                    } else {
                        item.active = ''
                    }
                }
            },
            tbmMenuPage(code) {
                if (this.mainCode == code) return;
                if (this.mainCode) {
                    JQ('#tbm-sub-' + this.mainCode).slideUp();
                }
                this.subMenuList = [];
                for (let item of this.menuList) {
                    if (item.code == code) {
                        this.menuTitle = item.mname;
                        if (item.subList && item.subList.length > 0) {
                            for (let subItem of item.subList) {
                                this.subMenuList.push(subItem);
                            }
                        }
                        this.mainCode = code;
                        item.active = 'active'
                        JQ('#tbm-sub-' + code).slideDown();

                    } else {
                        item.active = ''
                    }
                }
            },
            goSubMenuPage(code, subCode,item) {
                if (item.linkType == 'url') {
                    if (item.urlWinType == 'new') {
                        window.open(item.urlLink);
                    } else {
                        location.href = item.urlLink;
                    }
                    return;
                }

                let selectedItem = this.setSubMenu(code, subCode)
                if (JQ('.menu-pannel-layout').css('display') != 'none') {
                    JQ('.tbm-menu-box').css('margin-left', '100%');
                    JQ('.menu-pannel-layout').hide();
                }
                if (selectedItem) this.linkMove(selectedItem);
            },
            setSubMenu(code, subCode) {
                this.mainCode = code;
                this.subMenuList = [];
                let selectedItem = {};
                for (let item of this.menuList) {
                    if (item.code == code) {
                        if (item.subList && item.subList.length > 0) {
                            for (let subItem of item.subList) {
                                if (subCode == subItem.code) {
                                    subItem.active = 'active';
                                    selectedItem = subItem;
                                } else subItem.active = '';
                                this.subMenuList.push(subItem);

                            }
                        }
                        item.active = 'active'
                    } else {
                        if (item.subList && item.subList.length > 0) {
                            for (let subItem of item.subList) {
                                subItem.active = '';
                            }
                        }
                        item.active = ''
                    }

                }
                return selectedItem;
            },
            linkMove(item) {

                const subCode = (this.mainCode == item.code) ? 'none' : item.code;
                switch (item.linkType) {
                    case 'customize':
                        this.$router.push('/content/' + this.mainCode + '/' + subCode + '/' + item.customizeId).catch(() => {
                        });

                        break;
                    case 'url':
                        if (item.urlWinType == 'new') {
                            window.open(item.urlLink);
                        } else {
                            location.href = item.urlLink;
                        }
                        break;
                    case 'board':
                        if (item.boardType == 'view') {
                            this.$router.push('/board/articleView/' + this.mainCode + '/' + subCode + '/' + item.articleId).catch(() => {
                            });

                        } else {
                            this.$router.push('/board/articleList/' + this.mainCode + '/' + subCode + '/' + item.boardId).catch(() => {
                            });
                        }

                        break;
                    case 'no':
                        if (item.subList && item.subList.length > 0) {
                            item.subList[0].active = 'active';
                            this.linkMove(item.subList[0]);
                        }
                        break;
                }
            },
            goPage(path) {
                this.menuActiveReset()
                if (JQ('.menu-pannel-layout').css('display') != 'none') {
                    JQ('.tbm-menu-box').css('margin-left', '100%');
                    JQ('.menu-pannel-layout').hide();
                }
                this.$router.push(path)
            },
            goSubPage(code, itemData) {
                if (itemData.linkType == 'url') {
                    if (itemData.urlWinType == 'new') {
                        window.open(itemData.urlLink);
                    } else {
                        location.href = itemData.urlLink;
                    }
                    return;
                }
                let reset = false;
                for (let item of this.subMenuList) {
                    if (item.code == code) {
                        item.active = 'active'
                        if (item.type == 'mypage') {
                            reset = true;
                            switch (code) {
                                case 'mypageMain':
                                    this.$router.push('/mypage/main');
                                    break;
                                case 'mypageMemberModify':
                                    this.$router.push('/mypage/memberModify');
                                    break;
                                case 'mypageMemberPassword':
                                    this.$router.push('/mypage/memberPassword');
                                    break;
                            }
                        } else {
                            this.linkMove(item);
                        }

                    } else {
                        item.active = ''
                    }
                }
                if (reset) this.menuActiveReset();


            },
            goViewArticle(item) {

                if (item.gtype == 'comment') {
                    if (this.boardIds[item.bid]?.mainCode) {
                        sessionStorage.setItem('viewComment', item.gid);
                        const subCode = (this.boardIds[item.bid].subCode) ? this.boardIds[item.bid].subCode : 'none';
                        this.$router.push('/board/articleView/' + this.boardIds[item.bid].mainCode + '/' + subCode + '/' + item.parentId)
                    }
                } else {
                    if (this.boardIds[item.bid]?.mainCode) {
                        const subCode = (this.boardIds[item.bid].subCode) ? this.boardIds[item.bid].subCode : 'none';
                        this.$router.push('/board/articleView/' + this.boardIds[item.bid].mainCode + '/' + subCode + '/' + item.gid)
                    }
                }

            },
            logOut() {
                if (!this.isLogin) {

                    const params = {
                        message: '먼저 로그인하세요'
                    }
                    this.$eventBus.$emit('modalOpen', params)
                    return;
                }

                apiService.post(API_URL_LOGOUT, {}, (res) => {
                    if (res.status == 'success') {
                        AUTH.logout();
                        location.href = '/';
                    }

                })

            },
            validate() {
                if (!this.keyword) {
                    let params = {message: '검색어를 입력하세요'};
                    this.$eventBus.$emit('modalOpen', params)
                    return;
                }
                if (this.currentUrlName == 'siteSearch') {
                    this.$eventBus.$emit('searchKeyword', {keyword: this.keyword})
                } else {
                    this.$router.push('/search/' + this.keyword)
                }
            },
            menuSearchValidate() {
                if (!this.keyword) {
                    let params = {message: '검색어를 입력하세요'};
                    this.$eventBus.$emit('modalOpen', params)
                    return;
                }
                if (this.currentUrlName == 'siteSearch') {
                    this.$eventBus.$emit('searchKeyword', {keyword: this.keyword})
                } else {
                    this.$router.push('/search/' + this.keyword)
                }
                JQ('.tbm-menu-box').css('margin-left', '100%');
                JQ('.menu-pannel-layout').hide();
            },
            goAgreePage(code) {
                this.menuActiveReset()
                this.$router.push('/member/agree/' + code)
            },
            menuActiveReset() {
                for (let item of this.menuList) {
                    if (item.subList && item.subList.length > 0) {
                        for (let subItem of item.subList) {
                            subItem.active = '';
                        }
                    }
                    item.active = ''
                }
            },
            bodyClick(e) {
                this.$eventBus.$emit('bodyClick')

            },
            goLocationMove(cmd) {
                let url = '';
                switch (cmd) {
                    case 'slack':
                        url = 'https://hamonikr.slack.com';
                        break;
                    case 'git':
                        url = 'https://github.com/hamonikr';
                        break;
                }
                window.open(url)
            },
            showMyPannel() {
                JQ('.mypage-list').slideToggle(200);
                JQ('.mynotice-list').slideUp(200);
            },
            showUserNotice() {
                JQ('.mynotice-list').slideToggle(200);
                JQ('.mypage-list').slideUp(200);
            },
            handleResize() {
                const width = window.innerWidth;
                this.contentBodyClass = 'default';
                if (width >= 1260) {
                    this.bottomShow = 'show'
                    this.myMenuShow = false;
                    this.menuPannelShow = false;
                    if (this.$route.params.mainCode) {
                        this.searchShow = true;
                    }
                    switch (this.currentUrlName) {
                        case 'siteMain':
                        case 'siteSearch':
                            this.searchShow = true;
                            break;
                        case 'mypageMain':
                        case 'mypageMemberModify':
                        case 'mypageMemberPassword':
                            this.searchShow = false;
                            break;
                        default:
                            if (!this.$route.params.mainCode) {
                                this.searchShow = false;
                            }
                            break;
                    }

                } else {
                    if (width < 450) {
                        this.contentBodyClass = 'white';
                    }
                    this.myMenuShow = false;
                    this.menuPannelShow = true;
                    switch (this.currentUrlName) {
                        case 'siteMain':
                            this.bottomShow = 'show'
                            this.searchShow = true;
                            break;
                        case 'siteSearch':
                            this.bottomShow = 'none'
                            this.searchShow = true;
                            this.contentBodyClass = '';
                            break;
                        case 'mypageMain':
                        case 'mypageMemberModify':
                        case 'mypageMemberPassword':
                            this.myMenuShow = true;
                            this.contentBodyClass = 'white';
                            this.bottomShow = 'none'
                            this.searchShow = false;
                            break;

                        case 'boardArticleList':
                        case 'boardArticleRegist':
                        case 'boardArticleView':
                        case 'contentView':
                            this.contentBodyClass = 'white';
                            this.bottomShow = 'none'
                            this.searchShow = false;
                            break;

                        default:
                            this.bottomShow = 'none'
                            this.searchShow = false;
                            break;

                    }

                }

            }
        },
        mounted() {

            window.addEventListener('resize', this.handleResize);
        },
        updated() {
            let maxHeight = 0;
            JQ('.menu-pannel .sub-menu').each(function () {
                if (JQ(this).height() > maxHeight) maxHeight = JQ(this).height();

            })

            JQ('.menu-pannel .sub-menu-screen').css('height', (maxHeight + 40) + 'px');

        },
        beforeDestroy() {
            window.removeEventListener('resize', this.handleResize);
        },

    }
